// Misc.
$misc: (
  z-index-base: 10000
);

// Duration.
$duration: (
  menu: 0.35s,
  transition: 0.2s,
  banner: 2.5s
);

// Size.
$size: (
  border-radius: 4px,
  element-height: 2.75em,
  element-margin: 2em,
  inner: 65em
);

// Font.
$font: (
  family: (
    "Source Sans Pro",
    Helvetica,
    sans-serif
  ),
  family-fixed: (
    "Courier New",
    monospace
  ),
  weight: 300,
  weight-bold: 600,
  letter-spacing: 0.025em,
  letter-spacing-sub: 0.15em,
  letter-spacing-alt: 0.25em
);

// Palette.
$palette: (
  bg: black,
  bg-alt: #2a2f4a,
  fg: #ffffff,
  font2: black,
  fg-bold: #ffffff,
  primary: #561211,
  //#660000,
    primary-light: #a50101,
  secondary: #d9d9d9,
  //#ffb61e,
    tricary: rgb(223, 156, 2),
  fg-light: rgba(244, 244, 255, 0.2),
  border: rgba(212, 212, 255, 0.1),
  border-bg: rgba(212, 212, 255, 0.035),
  banner-bg: rgba(20, 20, 20, 0.3),
  highlight: #d9d9d9,
  accent1: #6fc3df,
  accent2: #8d82c4,
  accent3: #ec8d81,
  accent4: #e7b788,
  accent6: #87c5a4,
  accent7: rgba(20, 20, 20, 0.3),
  accent8: gray
);

// Logo.
$image: (
  logo2: "../../static/images/logo2.png"
);
