/* Button */

a.button-bottom {
  padding: 100px 240px;
  background: white; //url(../../static/images/btn-background.png);
  background-size: cover;
  margin: 0px auto 150px auto;
  background-repeat: no-repeat;
  object-fit: cover;
  justify-content: center;
  align-items: center;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-direction: column;
  -moz-transition: all 0.25s ease-in-out;
  -o-transition: all 0.25s ease-in-out;
  -webkit-transition: all 0.25s ease-in-out;

  p {
    position: absolute;
    text-align: Center;
    text-transform: uppercase;
    margin: 10px 0 15px 0;
  }

  p:nth-child(1) {
    font-size: 35px;
    font-weight: 700;
    margin-top: -15px;
    padding-bottom: 20px;
    color: _palette(primary);
  }

  p:nth-child(2) {
    //font-family: "soleil";
    font-weight: 300;
    color: black;
    font-size: 30px;
  }
}

.bottom-buttons {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 1800px;
  margin: 0 auto;

  a {
    margin: 15px;
    -webkit-transition: all 0.25s ease-in-out;
  }
}

input[type="submit"],
input[type="reset"],
input[type="button"],
button,
.button {
  @include vendor("appearance", "none");
  @include vendor(
    "transition",
    (
      "background-color #{_duration(transition)} ease-in-out",
      "box-shadow #{_duration(transition)} ease-in-out",
      "color #{_duration(transition)} ease-in-out"
    )
  );
  background-color: transparent;
  border: 0;
  border-radius: 0;
  box-shadow: inset 0 0 0 2px _palette(fg-bold);
  color: _palette(fg-bold);
  cursor: pointer;
  display: inline-block;
  font-size: 0.8em;
  font-weight: _font(weight-bold);
  height: 3.5em;
  letter-spacing: _font(letter-spacing-alt);
  line-height: 3.5em;
  padding: 0 1.75em;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  white-space: nowrap;

  &:hover,
  &:active {
    box-shadow: inset 0 0 0 2px _palette(highlight);
    color: _palette(highlight);
  }

  &:active {
    background-color: transparentize(_palette(highlight), 0.9);
    box-shadow: inset 0 0 0 2px desaturate(darken(_palette(highlight), 15), 5);
    color: desaturate(darken(_palette(highlight), 15), 5);
  }

  &.icon {
    &:before {
      margin-right: 0.5em;
    }
  }

  &.fit {
    display: block;
    margin: 0 0 (_size(element-margin) * 0.5) 0;
    width: 100%;
  }

  &.small {
    font-size: 0.6em;
  }

  &.big {
    font-size: 1.25em;
    height: 3em;
    line-height: 3em;
  }

  &.next {
    padding-right: 4.5em;
    position: relative;

    &:before,
    &:after {
      @include vendor(
        "transition",
        "opacity #{_duration(transition)} ease-in-out"
      );
      background-position: center right;
      background-repeat: no-repeat;
      background-size: 36px 24px;
      content: "";
      display: block;
      height: 100%;
      position: absolute;
      right: 1.5em;
      top: 0;
      vertical-align: middle;
      width: 36px;
    }

    &:before {
      background-image: svg-url(
        '<svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="36px" height="24px" viewBox="0 0 36 24" zoomAndPan="disable"><style>line { stroke: #{_palette(fg-bold)}; stroke-width: 2px; }</style><line x1="0" y1="12" x2="34" y2="12" /><line x1="25" y1="4" x2="34" y2="12.5" /><line x1="25" y1="20" x2="34" y2="11.5" /></svg>'
      );
    }

    &:after {
      background-image: svg-url(
        '<svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="36px" height="24px" viewBox="0 0 36 24" zoomAndPan="disable"><style>line { stroke: #{_palette(highlight)}; stroke-width: 2px; }</style><line x1="0" y1="12" x2="34" y2="12" /><line x1="25" y1="4" x2="34" y2="12.5" /><line x1="25" y1="20" x2="34" y2="11.5" /></svg>'
      );
      opacity: 0;
      z-index: 1;
    }

    &:hover,
    &:active {
      &:after {
        opacity: 1;
      }
    }

    @include breakpoint(large) {
      padding-right: 5em;
    }
  }

  &.special {
    background-color: _palette(fg-bold);
    box-shadow: none;
    color: _palette(bg);

    &:hover,
    &:active {
      background-color: _palette(highlight);
      color: _palette(bg) !important;
    }

    &:active {
      background-color: desaturate(darken(_palette(highlight), 15), 5);
    }
  }

  &.disabled,
  &:disabled {
    @include vendor("pointer-events", "none");
    cursor: default;
    opacity: 0.25;
  }
}
